<script>
import axios from "axios";
import imgPreview from "../components/PreviewImg";

export default {
  components: {
    imgPreview
  },
  data() {
    return {
      imagePreviewURL: "",
      isLoading: false,
      isFormValid: false,
      showContent: {
        seller: false,
        buyer: false,
        product: false
      },
      activeBtn: 0,
      typeOfArgi: [],
      argiTd: [],
      mini: false,
      drawer: true,
      userInfo: [],
      productMk: [],
      groupName: "",
      isLoading: false,
      baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/"
    };
  },
  computed: {
    cardWidth() {
      return this.mini ? 56 : 256;
    }
  },
  methods: {
    editProduct(idProductOfMemberMk) {
      this.$router.push({
        name: "EditProduct",
        params: { idProductOfMemberMk: idProductOfMemberMk }
      });
    },
    errMsg(msg) {
      this.$swal.fire({
        icon: "info",
        title: msg,
        showConfirmButton: true,
        allowOutsideClick: false
      });
      this.remove(this.productForm.img.length - 1);
    },
    findProductName(type) {
      this.productByArgiType = [];
      this.productByArgiType = this.argiTd.filter(e => {
        return e.TypeOfArgiIdTypeOfArgi === type;
      });
      console.log("productByArgiType", this.productByArgiType);
    },
    showAddProduct() {
      this.showContent = {
        seller: false,
        buyer: false,
        product: true
      };
    },
    getUserInfo() {
      let username = new FormData();
      username.append("username", localStorage.user);
      if (localStorage.typeOfMember === "Seller") {
        username.append("table", "SellerMember_MK");
        axios
          .post(`${this.baseUrl}member/postFindSellerInfo.php`, username)
          .then(res => {
            if (res.data.sellerUser !== 0) {
              this.userInfo = res.data.sellerUser[0];
              this.userInfo.sellerImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/profile/${res.data.sellerUser[0].sellerImg}`;
              this.showContent.seller = true;
              console.log("userInfo", this.userInfo);
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        axios
          .post(`${this.baseUrl}member/postFindUserInfo.php`, username)
          .then(res => {
            if (res.data.buyerUser !== 0) {
              this.userInfo = res.data.buyerUser[0];
              this.userInfo.buyerImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/profile/${res.data.buyerUser[0].buyerImg}`;
              this.showContent.buyer = true;
              console.log("userInfo", this.userInfo);
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    checkLogin() {
      if (
        localStorage.idMember === "" ||
        localStorage.idMember === null ||
        localStorage.idMember === undefined
      ) {
        this.$router.push({ name: "Login" });
      }
    },
    showUserProfile() {
      this.showContent.product = false;
      if (localStorage.typeOfMember === "Seller") {
        this.showContent.seller = true;
      } else {
        this.showContent.buyer = true;
      }
    },
    productMember() {
      let idSeller = localStorage.idMember;
      const formData = new FormData();
      formData.append("idSellerMemberMk", idSeller);
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/product/getProductBySeller.php",
          formData
        )
        .then(res => {
          this.productMk = res.data.productMk;
          if ((this.groupName = this.productMk[0].groupName !== "")) {
            this.groupName = this.productMk[0].groupName;
          }
          console.log("productMk", this.productMk);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    this.checkLogin();
    this.getUserInfo();
    this.productMember();
  },
  created() {}
};
</script>



