<template>
  <div>
    <v-container>
      <div class="row">
        <div class="col-md-3 col-sm-3 col-xs-12">
          <v-card outlined>
            <v-card-title class="green darken-1 white--text"
              >ประเภทเกษตรกร</v-card-title
            >
            <v-divider></v-divider>
            <template>
              <!-- <v-treeview
                :items="items"
                :open="[1]"
                :active="[5]"
                :selected-color="'#fff'"
                activatable
                open-on-click
                dense
              ></v-treeview> -->
            </template>
            <v-list>
              <v-list-item-group v-model="typeItemsSelected" color="green">
                <v-list-item
                  @click="changeFarmer(typeItemsSelected)"
                  v-for="(item, i) in typeItems"
                  :key="i"
                  :value="item.idTypeOfSellerMk"
                >
                  <v-list-item-content>
                    <v-list-item-title class="pa-3">
                      <v-row cols="12">
                        {{ item.nameTypeOfSeller }}<v-spacer></v-spacer>
                        {{
                          item.nameTypeOfSeller === "เกษตรกรรายเดี่ยว"
                            ? `(${countType.first})`
                            : item.nameTypeOfSeller === "วิสาหกิจชุมชน"
                            ? `(${countType.second})`
                            : item.nameTypeOfSeller === "สหกรณ์"
                            ? `(${countType.third})`
                            : item.nameTypeOfSeller === "กลุ่มเกษตรกร"
                            ? `(${countType.fourth})`
                            : item.nameTypeOfSeller === "กลุ่มไม่เป็นทางการ"
                            ? `(${countType.fifth})`
                            : ""
                        }}
                      </v-row>
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
            <v-divider></v-divider>
            <v-card-title class="green darken-1 white--text"
              >เงื่อนไขการค้นหา</v-card-title
            >
            <v-card-title>ช่วงราคา</v-card-title>
            <v-row class="pa-2" dense>
              <v-col cols="12" sm="5">
                <v-text-field
                  v-model="min"
                  label="ขั้นต่ำ"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="2">
                <p class="pt-2 text-center">ถึง</p>
              </v-col>
              <v-col cols="12" sm="5">
                <v-text-field
                  v-model="max"
                  label="สูงสุด"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-card-title class="pb-0">ค้นหาสินค้าในเดือน</v-card-title>
            <v-container class="pt-0 mx-auto" fluid>
              <v-row>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox
                    v-model="month"
                    label="ม.ค."
                    value="1"
                    hide-details
                  ></v-checkbox>
                  <v-checkbox
                    v-model="month"
                    label="เม.ย."
                    value="4"
                    hide-details
                  ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox
                    v-model="month"
                    label="ก.พ."
                    value="2"
                    hide-details
                  ></v-checkbox>
                  <v-checkbox
                    v-model="month"
                    label="พ.ค."
                    value="5"
                    hide-details
                  ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox
                    v-model="month"
                    label="มี.ค."
                    value="3"
                    hide-details
                  ></v-checkbox>
                  <v-checkbox
                    v-model="month"
                    label="มิ.ย."
                    value="6"
                    hide-details
                  ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox
                    v-model="month"
                    label="ก.ค."
                    value="7"
                    hide-details
                  ></v-checkbox>
                  <v-checkbox
                    v-model="month"
                    label="ต.ค."
                    value="10"
                    hide-details
                  ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox
                    v-model="month"
                    label="ส.ค."
                    value="8"
                    hide-details
                  ></v-checkbox>
                  <v-checkbox
                    v-model="month"
                    label="พ.ย."
                    value="11"
                    hide-details
                  ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox
                    v-model="month"
                    label="ก.ย."
                    value="9"
                    hide-details
                  ></v-checkbox>
                  <v-checkbox
                    v-model="month"
                    label="ธ.ค."
                    value="12"
                    hide-details
                  ></v-checkbox>
                </v-col>
              </v-row>
            </v-container>
            <v-card-title class="pb-0">ค้นหาจากมาตรฐาน</v-card-title>
            <v-container class="pt-0 pa-4">
              <v-row
                class="mx-auto"
                cols="12"
                sm="4"
                md="4"
                v-for="type in typeOfStand"
                :key="type.idTypeOfStand"
              >
                <v-checkbox
                  v-model="standSelected"
                  :label="type.nameTypeOfStand"
                  :value="type.idTypeOfStand"
                  hide-details
                ></v-checkbox>
              </v-row>
            </v-container>
          </v-card>
        </div>
        <div class="col-md-9 col-sm-9 col-xs-12">
          <v-row dense>
            <v-col cols="12" sm="8" class="pl-6 pt-6"> </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="productName"
                label="ค้นหาสินค้าจากชื่อ"
                dense
              >
                <template v-slot:append>
                  <v-btn
                    depressed
                    tile
                    color="primary"
                    class="ma-0"
                    @click="filterProductName"
                  >
                    <v-icon>mdi-magnify</v-icon>
                  </v-btn>
                </template>
              </v-text-field>
              <v-select
                class="pa-0"
                v-model="select"
                :items="options"
                style="margin-bottom: -20px;"
                outlined
                dense
              ></v-select>
            </v-col>
          </v-row>

          <v-breadcrumbs
            class="pb-1 body-1 font-weight-bold"
            :items="breadcrums"
          ></v-breadcrumbs>

          <v-divider></v-divider>

          <v-row class="pa-5" justify="center" v-if="isLoading">
            <h1>ไม่พบข้อมูล...</h1>
          </v-row>
          <v-row class="pa-5" justify="center" v-if="isLoading">
            <v-progress-linear
              color="primary"
              indeterminate
              height="25"
            ></v-progress-linear>
          </v-row>
          <ProductList :productMk="filterProduct" />
        </div>
      </div>
    </v-container>
  </div>
</template>
<style>
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: 0.8;
  position: absolute;
  width: 100%;
}
</style>
<script>
import api from "../services/apis";
import ProductList from "../components/Shop/ProductList.vue";

export default {
  components: {
    ProductList
  },
  inject: ["mySpinner"],
  data: () => ({
    isLoading: false,
    productName: "",
    productMk: [],
    cloneProductMk: [],
    typeOfStand: [],
    countType: {
      first: 0,
      second: 0,
      third: 0,
      fourth: 0,
      fifth: 0
    },
    standSelected: [],
    typeItemsSelected: 0,
    range: [0, 1000],
    select: "ตัวกรองการค้นหา",
    options: ["ตัวกรองการค้นหา", "ราคา: ต่ำ ถึง สูง", "ราคา: สูง ถึง ต่ำ"],
    typeItems: [],
    page: 1,
    perPage: 12,
    sellerMember: [],
    breadcrums: [
      {
        text: "แสดงสินค้าจากประเภทเกษตรกร",
        disabled: false,
        href: "#"
      },
      {
        text: "ทั้งหมด",
        disabled: false,
        href: "#"
      }
    ],
    month: [],
    min: 0,
    max: 1000
  }),
  computed: {
    filterProduct() {
      return this.farmerFilter(
        this.filterRange(
          this.filterMonth(
            this.filterByStand(
              this.filterName(this.sortByPrice(this.productMk))
            )
          )
        )
      );
    },
    // pageChange: function() {
    //   console.log("pageChange", this.page);
    //   return this.page;
    // },
    checkProductSize: function() {
      if (this.productMk.length === 0 && this.mySpinner.isLoading === false) {
        return true;
      }
    },
    filterProductsByRange: function() {
      return this.productMk.filter(product =>
        product.priceBegin > 0 && product.priceEnd < this.range
          ? product
          : product
      );
    },
    filterProductName: function() {
      return this.productMk.filter(product => {
        return product.nameArgi.match(this.productName);
      });
    }
  },
  methods: {
    // Emit Event
    pageEmit(page) {
      this.page = page;
      console.log("pageEmit", this.page);
    },
    // End Emit Event
    filterName(products) {
      console.log(this.page);
      return products.filter(product => {
        return product.nameArgi.match(this.productName);
      });
    },
    filterByStand(products) {
      return products.filter(product => {
        return product.idTypeOfStand.includes(this.standSelected);
      });
    },
    filterMonth(products) {
      return products.filter(product => {
        return product.monthOfProduct
          .split(",")
          .map(x => +x)
          .sort((a, b) => {
            return a - b;
          })
          .toString()
          .includes(
            this.month
              .map(x => +x)
              .sort((a, b) => {
                return a - b;
              })
              .toString()
          );
      });
    },
    filterRange(products) {
      return products.filter(product =>
        parseInt(product.priceBegin) >= this.min &&
        parseInt(product.priceEnd) <= this.max
          ? product
          : ""
      );
    },
    changeFarmer(type) {
      this.page = 1;
      this.productName = "";
      this.month = [];
      this.standSelected = [];
      this.min = 0;
      this.max = 1000;
    },
    farmerFilter(products) {
      return products.filter(product =>
        parseInt(this.typeItemsSelected) !== 0 &&
        this.typeItemsSelected !== undefined
          ? parseInt(product.idTypeOfSellerMk) === this.typeItemsSelected
          : (this.productMk = [...this.cloneProductMk])
      );
    },
    sortByPrice(products) {
      return products.sort(
        this.select === "ราคา: ต่ำ ถึง สูง" || this.select === "ตัวกรองการค้นหา"
          ? (a, b) => parseFloat(a.priceEnd) - parseFloat(b.priceEnd)
          : (a, b) => parseFloat(b.priceEnd) - parseFloat(a.priceEnd)
      );
    },
    getAllProduct() {
      api
        .get("product/getAllProduct.php")
        .then(res => {
          if (res.data.productMk !== 0) {
            res.data.productMk.reverse();
            this.productMk = res.data.productMk;
            this.cloneProductMk = res.data.productMk;
            console.log("productMk", this.productMk);
            this.sellerMember.map((e, i) => {
              switch (e.idTypeOfSeller_MK) {
                case "1":
                  this.countType.first++;
                  break;
                case "2":
                  this.countType.second++;
                  break;
                case "3":
                  this.countType.third++;
                  break;
                case "4":
                  this.countType.fourth++;
                  break;
                case "5":
                  this.countType.fifth++;
                  break;
              }
            });
            this.mySpinner.isLoading = !this.mySpinner.isLoading;
            console.log("countType", this.countType);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getTypeOfSeller() {
      api
        .get("member/getTypeOfSeller.php")
        .then(res => {
          if (res.data.sellerType !== 0) {
            this.typeItems = res.data.sellerType;
            this.typeItems.unshift({
              idTypeOfSellerMk: "0",
              nameTypeOfSeller: "แสดงทุกประเภท"
            });
            console.log("typeItems", this.typeItems);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getAllSellerMember() {
      api
        .get("member/getAllSellerMember.php")
        .then(res => {
          this.sellerMember = res.data.sellerUser;
          console.log("SellerUser", this.sellerMember);
        })
        .then(() => {
          this.getAllProduct();
        })
        .catch(err => {
          console.log(err);
        });
    },
    getTypeOfStand() {
      api
        .get("product/getTypeOfStand.php")
        .then(res => {
          this.typeOfStand = res.data.typeOfStand;
          this.typeOfStand.shift();
          console.log("typeOfStand", this.typeOfStand);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  async mounted() {
    this.mySpinner.isLoading = !this.mySpinner.isLoading;
    await this.getTypeOfStand();
    await this.getTypeOfSeller();
    await this.getAllSellerMember();
  }
};
</script>



