<template>
  <div id="app">
    <Spinner :start="spin.isLoading" />
    <router-view />
  </div>
</template>

<script>
import Spinner from "@/components/Spinner/Spinner";
export default {
  name: "App",
  components: {
    Spinner
  },
  data() {
    return {
      spin: {
        isLoading: false
      }
    };
  },
  provide() {
    return {
      mySpinner: this.spin
    };
  },
  methods: {}
};
</script>



