<template>
  <v-container>
    <div class="row text-center">
      <div
        class="col-md-4 col-sm-6 col-xs-12"
        :key="index"
        v-for="(product, index) in visibleContentPages"
      >
        <v-hover v-slot:default="{ hover }">
          <v-card
            class="mx-auto"
            color="grey lighten-4"
            max-width="600"
            height="100%"
          >
            <v-img
              class="white--text align-end"
              :aspect-ratio="16 / 9"
              height="200px"
              :src="product.titleImg"
              :lazy-src="product.titleImg"
            >
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular
                    indeterminate
                    color="grey lighten-5"
                  ></v-progress-circular>
                </v-row>
              </template>
              <v-expand-transition>
                <div
                  v-if="hover"
                  class="d-flex transition-fast-in-fast-out white darken-4 v-card--reveal display-3"
                  style="height: 100%;"
                >
                  <v-btn
                    v-if="hover"
                    @click="productDetail(product.idProductOfMemberMk)"
                    color="primary"
                    outlined
                    >รายละเอียด</v-btn
                  >
                </div>
              </v-expand-transition>
            </v-img>
            <v-card-text class="pa-0">
              <v-row cols="12" justify="center">
                <v-card-title
                  class="indigo--text text--darken-4 font-weight-black"
                >
                  {{ product.nameArgi }}
                </v-card-title>
              </v-row>
              <v-card-subtitle class="font-weight-bold pt-0">
                <h4>{{ product.groupName }}</h4>
              </v-card-subtitle>

              <v-row justify="center">
                <v-chip class="body-2 green white--text mb-1">
                  <h5>{{ product.nameTypeOfArgi }}</h5>
                </v-chip>
              </v-row>
              <v-row justify="center"
                ><span class="body-1 font-weight-black pa-1"
                  >{{
                    product.priceBegin === product.priceEnd
                      ? `฿${product.priceEnd}`
                      : `฿${product.priceBegin} - ฿${product.priceEnd}`
                  }}
                  / {{ product.nameUnit }}</span
                ></v-row
              >
            </v-card-text>
          </v-card>
        </v-hover>
      </div>
    </div>
    <v-row class="mt-3" align="center" justify="center">
      <v-pagination
        @input="changePage($event)"
        v-model="page"
        :length="totalPage"
      ></v-pagination>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "ProductList",
  props: {
    productMk: Array
  },
  data() {
    return { page: 1, perPage: 12, pageChoose: 0 };
  },
  computed: {
    totalPage() {
      this.pageChoose = Math.ceil(this.productMk.length / this.perPage);
      return Math.ceil(this.productMk.length / this.perPage);
    },
    visibleContentPages: function() {
      if (this.page > this.pageChoose) {
        this.page = 1;
      }
      window.scroll(0, 0);
      return this.productMk.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    }
  },
  methods: {
    changePage(event) {
      this.pageChoose = event;
      console.log("event", event);
      // this.$emit("update:page", this.page);
    },
    productDetail(idProduct) {
      this.$router.push({
        name: "ProductDetail",
        params: { idProductOfMemberMk: idProduct }
      });
    }
  }
};
</script>

<style></style>



