<template>
  <v-col md="4">
    <v-img
      v-if="dataUrl"
      :src="dataUrl"
      :alt="file.name"
    >
    </v-img>
    <v-btn
      :color="file.name === chooseTitle ? 'primary' : ''"
      class="mt-2"
      @click="$emit('chooseTitleImg', file.name, imgIndex)"
    >
      รูปปก
    </v-btn>
    <span>{{ file.index }}</span>
  </v-col>
</template>
<script>
export default {
  props: ["file", "imgIndex", "chooseTitle"],
  data() {
    return {
      dataUrl: null,
      imgFile: this.file
    }
  },
  methods: {},
  created() {
    const reader = new FileReader()
    console.log(this.imgFile);

    reader.addEventListener(
      "load",
      () => {
        this.dataUrl = reader.result
      },
      false
    )

    reader.readAsDataURL(this.file)
  }
}
</script>


