import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/Home";
import Shop from "@/views/Shop";
import Product from "@/views/Product";
import Blog from "@/views/Blog";
import Market from "@/views/Market";
import Post from "@/views/Post";
import Cart from "@/views/Cart";
import Content from "@/views/Content";
import Layout from "@/views/Layout";
import Login from "@/views/Login";
import Register from "@/views/Register";
import AboutUs from "@/views/AboutUs";
import TypeList from "@/views/TypeList";
import FindProduct from "@/views/FindProduct";
import UserProfile from "@/views/UserProfile";
import EditProfile from "@/views/EditProfile";
import EditSeller from "@/views/EditSeller";
import ProductDetail from "@/views/ProductDetail";
import AddProduct from "@/views/AddProduct";
import EditProduct from "@/views/EditProduct";

Vue.use(Router);

export default new Router({
  mode: "hash",
  linkActiveClass: "active",
  base: "/farmdd",
  routes: [
    {
      path: "/",
      component: Layout,
      children: [
        {
          path: "/",
          component: Home,
          name: "Home"
        },
        {
          path: "/shop",
          component: Shop,
          name: "Shop"
        },
        {
          path: "/addproduct",
          component: AddProduct,
          name: "AddProduct"
        },
        {
          path: "/editproduct/:idProductOfMemberMk",
          component: EditProduct,
          name: "EditProduct"
        },
        {
          path: "/editprofile",
          component: EditProfile,
          name: "EditProfile"
        },
        {
          path: "/editprofile",
          component: EditProfile,
          name: "EditProfile"
        },
        {
          path: "/product",
          component: Product,
          name: "Product"
        },
        {
          path: "/market",
          component: Market,
          name: "Market"
        },
        {
          path: "/blog",
          component: Blog,
          name: "Blog"
        },
        {
          path: "/aboutus",
          component: AboutUs,
          name: "AboutUs"
        },
        {
          path: "/userprofile",
          component: UserProfile,
          name: "UserProfile"
        },
        {
          path: "/editseller",
          component: EditSeller,
          name: "EditSeller"
        },
        {
          path: "/findproduct",
          component: FindProduct,
          name: "FindProduct"
        },
        {
          path: "/typelist/:idTypeOfArgi/:nameTypeOfArgi",
          component: TypeList,
          name: "TypeList"
        },
        {
          path: "/content/:page",
          component: Content,
          name: "Content"
        },
        {
          path: "/productdetail/:idProductOfMemberMk",
          component: ProductDetail,
          name: "ProductDetail"
        },
        {
          path: "/post/:id",
          component: Post,
          name: "Post"
        },
        {
          path: "/register",
          component: Register,
          name: "Register"
        },
        {
          path: "/cart",
          component: Cart,
          name: "Cart"
        },
        {
          path: "/login",
          component: Login,
          name: "Login"
        }
      ]
    }
  ]
});



